.topMenu {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  box-sizing: border-box;
  display: flex;
  align-content: center;
  justify-content: space-between;
  color: #fff;
  background-color: #026aa7;
}
.topMenu__topLeftMenu, .topMenu__topRightMenu, .topMenu__logo {
  box-sizing: border-box;
  padding: 4px;
  height: 40px;
}

.topLeftMenu,
.topRightMenu {
  display: flex;
  align-content: center;
  justify-content: flex-start;
}
.topLeftMenu__btn,
.topRightMenu__btn {
  min-width: 32px;
  margin-right: 4px;
  border-radius: 3px;
  cursor: pointer;
  text-decoration: none;
  background-color: rgba(255, 255, 255, 0.3);
  line-height: 32px;
  font-size: 14px;
  text-align: center;
  white-space: nowrap;
  -webkit-transition: 100ms, ease;
  transition: 100ms, ease;
}
.topLeftMenu__btn:hover,
.topRightMenu__btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
}
.topLeftMenu__btn--searchInput,
.topRightMenu__btn--searchInput {
  padding-left: 5px;
  background-color: transparent;
  border-radius: 3px;
  border: none;
  box-sizing: border-box;
  color: rgba(255, 255, 255, 0.5);
  font-size: 13px;
  line-height: 30px;
  outline: none;
  -webkit-transition: width 0.15s;
  transition: width 0.15s;
  width: 180px;
}
.topLeftMenu__btn--searchInput:focus,
.topRightMenu__btn--searchInput:focus {
  width: 380px;
  background-color: #fff;
  color: #000;
}
.topLeftMenu__btn--boardsLink:after,
.topRightMenu__btn--boardsLink:after {
  content: "Доски";
  margin: 0 4px;
}

/*# sourceMappingURL=top_menu.css.map */

.projectsWindow {
  width: 75vw;
  margin: 50px auto;
  padding: 10px;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 10px;
  justify-content: center;
  align-items: start;
}

.project {
  min-height: 100px;
  padding: 5px;
  box-sizing: border-box;
  background-color: darkcyan;
  border: 2px solid #000;
  border-radius: 5px;
  cursor: pointer;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: -webkit-transform 200ms ease;
  transition: -webkit-transform 200ms ease;
  transition: transform 200ms ease;
  transition: transform 200ms ease, -webkit-transform 200ms ease;
}
.project:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.project--new {
  background-color: rgba(0, 0, 0, 0.3);
}
.project__title {
  color: #000;
  font-size: 14px;
  font-weight: bold;
}

/*# sourceMappingURL=project_page.css.map */

.boardsWindow {
  margin: 50px auto;
  padding: 10px;
  box-sizing: border-box;
}
.boardsWindow__header {
  margin-bottom: 10px;
  font-size: 25px;
  line-height: 30px;
  font-weight: bold;
  border-bottom: 2px solid #000;
}
.boardsWindow__boards {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
}

.board {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 25vw;
  min-height: 100px;
  margin-right: 20px;
  padding: 5px;
  box-sizing: border-box;
  background-color: khaki;
  border: 2px solid #000;
  border-radius: 5px;
  cursor: pointer;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: -webkit-transform 200ms ease;
  transition: -webkit-transform 200ms ease;
  transition: transform 200ms ease;
  transition: transform 200ms ease, -webkit-transform 200ms ease;
}
.board:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.board--new {
  background-color: rgba(0, 0, 0, 0.3);
}
.board__title {
  color: #000;
  font-size: 14px;
  font-weight: bold;
  border-bottom: 2px solid black;
}
.board__newtask {
  justify-self: flex-end;
  background-color: rgba(0, 0, 0, 0.2);
  border: 1px solid #000;
  border-radius: 5px;
  padding: 0 5px;
  text-align: right;
}

.taskWindow {
  display: flex;
  flex-direction: column;
  margin: 5px 0;
}
.taskWindow__task {
  margin-top: 5px;
  padding: 5px;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 5px;
}
.taskWindow__task--complete {
  border: 1px solid #28ac28;
}

.task__title {
  font-size: 14px;
  border-bottom: 1px solid #000;
}
.task__description {
  margin-top: 10px;
  font-size: 14px;
}

/*# sourceMappingURL=boards_page.css.map */

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
}

.modalWindow {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #fff;
  color: #000;
  padding: 5px;
  box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.75);
}
.modalWindow__header {
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #000;
}
.modalWindow__body {
  padding: 10px;
  border-bottom: 1px solid #000;
}
.modalWindow__footer {
  padding: 10px;
  display: flex;
  justify-content: flex-end;
  align-content: center;
}

.header__title {
  line-height: 30px;
  font-size: 20px;
  font-weight: bold;
  color: #000;
}
.header__closeBtn {
  width: 20px;
  height: 20px;
  margin: 0;
  padding: 0;
  background-color: transparent;
  color: #999;
  font-size: 16px;
  font-weight: bold;
  border: 1px solid #999;
  box-sizing: border-box;
}
.header__closeBtn:hover {
  color: #000;
  border-color: #000;
}

.footer__closeBtn, .footer__submitBtn {
  background-color: transparent;
  color: #000;
  font-size: 16px;
  font-weight: bold;
  border: 1px solid #000;
  box-sizing: border-box;
  text-decoration: none;
  -webkit-transition: 300ms ease;
  transition: 300ms ease;
}
.footer__closeBtn {
  margin-right: 10px;
}
.footer__closeBtn:hover {
  background-color: lightsalmon;
}
.footer__submitBtn:hover {
  background-color: palegreen;
}

/*# sourceMappingURL=modal.css.map */

.projectTitleInput,
.boardTitleInput,
.taskTitleInput,
.taskDescriptionInput {
  margin-top: 5px;
  width: 25vw;
  outline: none;
  border: 1px solid black;
  border-radius: 5px;
  padding: 3px;
  font-size: 14px;
}

.projectTitleLength,
.boardTitleLength,
.taskTitleLength,
.taskDescriptionLength {
  font-size: 10px;
  color: #333;
  text-align: right;
}

